import { getStatuses } from '../../lib/statuses'
import { Key } from './Key'
import { useContext } from 'react'
import { ENTER_TEXT, DELETE_TEXT } from '../../constants/strings'
import { solutionContext } from '../context'

type Props = {
  onChar: (value: string) => void
  onDelete: () => void
  onEnter: () => void
  onReset: () => void
  keyboardKeys: string[]
  guesses: string[]
  isRevealing?: boolean
}

export const Keyboard = ({
  onChar,
  onDelete,
  onEnter,
  onReset,
  keyboardKeys,
  guesses,
  isRevealing,
}: Props) => {
  let curSolution = useContext(solutionContext)
  const charStatuses = getStatuses(guesses, curSolution)

  const onClick = (value: string) => {
    if (value === 'ENTER') {
      onEnter()
    } else if (value === 'DELETE') {
      onDelete()
    } else if (value === 'RESET') {
      onReset()
    } else {
      onChar(value)
    }
  }

  // useEffect(() => {
  //   const listener = (e: KeyboardEvent) => {
  //     if (e.code === 'Enter') {
  //       onEnter()
  //     } else if (e.code === 'Backspace') {
  //       onDelete()
  //     } else {
  //       const key = localeAwareUpperCase(e.key)
  //       // TODO: check this test if the range works with non-english letters
  //       if (key.length === 1 && key >= 'A' && key <= 'Z') {
  //         onChar(key)
  //       }
  //     }
  //   }
  //   window.addEventListener('keyup', listener)
  //   return () => {
  //     window.removeEventListener('keyup', listener)
  //   }
  // }, [onEnter, onDelete, onChar])
  // 0..10
  // ['Q', 'W', 'E', 'R', 'T', 'Y', 'U', 'I', 'O', 'P'];
  const rowOne = keyboardKeys.slice(0, 10)
  // 10..19
  // ['A', 'S', 'D', 'F', 'G', 'H', 'J', 'K', 'L'];
  const rowTwo = keyboardKeys.slice(10, 20)
  // 19..
  // ['Z', 'X', 'C', 'V', 'B', 'N', 'M']
  const rowThree = keyboardKeys.slice(20, 26);

  return (
    <div>
      <div className="flex justify-center mb-2">
      </div>
      <div className="flex justify-center mb-1">
        {rowOne.map((key) => (
          <Key
            value={key}
            key={key}
            onClick={onClick}
            status={charStatuses[key]}
            isRevealing={isRevealing}
          />
        ))}
      </div>
      <div className="flex justify-center mb-1">
        {rowTwo.map((key) => (
          <Key
            value={key}
            key={key}
            onClick={onClick}
            status={charStatuses[key]}
            isRevealing={isRevealing}
          />
        ))}
      </div>
      <div className="flex justify-center">
        <Key width={67} value="DELETE" onClick={onClick}>
          {DELETE_TEXT}
        </Key>
        {rowThree.map((key) => (
          <Key
            value={key}
            key={key}
            onClick={onClick}
            status={charStatuses[key]}
            isRevealing={isRevealing}
          />
        ))}
        <Key width={67} value="ENTER" onClick={onClick}>
          {ENTER_TEXT}
        </Key>
      </div>
    </div>
  )
}
