import { WORDS } from '../constants/wordlist'
import { VALID_GUESSES } from '../constants/validGuesses'
import { WRONG_SPOT_MESSAGE, NOT_CONTAINED_MESSAGE } from '../constants/strings'
import { getGuessStatuses } from './statuses'
import { default as GraphemeSplitter } from 'grapheme-splitter'

export const isWordInWordList = (word: string) => {
  return (
    WORDS.includes(localeAwareLowerCase(word)) ||
    VALID_GUESSES.includes(localeAwareLowerCase(word))
  )
}

export const isWinningWord = (word: string, solution: string) => {
  return solution === word
}

// build a set of previously revealed letters - present and correct
// guess must use correct letters in that space and any other revealed letters
// also check if all revealed instances of a letter are used (i.e. two C's)
export const findFirstUnusedReveal = (word: string, guesses: string[], curSolution: string) => {
  if (guesses.length === 0) {
    return false
  }

  const lettersLeftArray = new Array<string>()
  const guess = guesses[guesses.length - 1]
  const statuses = getGuessStatuses(guess, curSolution)
  const splitWord = unicodeSplit(word)
  const splitGuess = unicodeSplit(guess)

  for (let i = 0; i < splitGuess.length; i++) {
    if (statuses[i] === 'correct' || statuses[i] === 'present') {
      lettersLeftArray.push(splitGuess[i])
    }
    if (statuses[i] === 'correct' && splitWord[i] !== splitGuess[i]) {
      return WRONG_SPOT_MESSAGE(splitGuess[i], i + 1)
    }
  }

  // check for the first unused letter, taking duplicate letters
  // into account - see issue #198
  let n
  for (const letter of splitWord) {
    n = lettersLeftArray.indexOf(letter)
    if (n !== -1) {
      lettersLeftArray.splice(n, 1)
    }
  }

  if (lettersLeftArray.length > 0) {
    return NOT_CONTAINED_MESSAGE(lettersLeftArray[0])
  }
  return false
}

export const unicodeSplit = (word: string) => {
  return new GraphemeSplitter().splitGraphemes(word)
}

export const unicodeLength = (word: string) => {
  return unicodeSplit(word).length
}

export const localeAwareLowerCase = (text: string) => {
  return process.env.REACT_APP_LOCALE_STRING
    ? text.toLocaleLowerCase(process.env.REACT_APP_LOCALE_STRING)
    : text.toLowerCase()
}

export const localeAwareUpperCase = (text: string) => {
  return process.env.REACT_APP_LOCALE_STRING
    ? text.toLocaleUpperCase(process.env.REACT_APP_LOCALE_STRING)
    : text.toUpperCase()
}

export const getWordOfDay = () => {
  // January 1, 2022 Game Epoch
  const epochMs = new Date(2022, 0).valueOf()
  const now = Date.now()
  const msInDay = 86400000
  const index = Math.floor((now - epochMs) / msInDay)
  const nextday = (index + 1) * msInDay + epochMs

  return {
    solution: localeAwareUpperCase(WORDS[index % WORDS.length]),
    solutionIndex: index,
    tomorrow: nextday,
  }
}

export const getOneWordOfDay = (): string => {

  function getRandomInt(min: number, max: number) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min + 1)) + min;
  }

  let idx = getRandomInt(0, WORDS.length-1)
  return localeAwareUpperCase(WORDS[idx]);
}

export const shuffleList = (words: string[]) => {
  let newWords = [...words]
  for (var i = newWords.length - 1; i > 0; i--) {
    var j = Math.floor(Math.random() * (i + 1));
    var temp = newWords[i];
    newWords[i] = newWords[j];
    newWords[j] = temp;
  }
  return newWords;
}

export const subSetOf = ( subSet: Set<string>, list: string[]): boolean => {
  for (let s in list) {
    if (!subSet.has(s)){
      return false;
    }
  }
  return true;
}

const shareWords = (phraseOne: string, phraseTwo: string): number => {
  let res = 0
  for (let c of phraseOne) {
    if (phraseTwo.includes(c)) {
      res++;
    }
  }
  return res
} 

export const getAdjPhrases = (phrase: string, shuffleList: string[], takes: number=5): string[] => {
  let res = [];
  for (let chinesePhrase of shuffleList) {
    if (shareWords(chinesePhrase, phrase) > 0 && takes > 0) {
      res.push(chinesePhrase)
      takes--;
    }
    if (takes <= 0){
      break;
    }
  }
  return res;
}

export const getKeyboardKeys = (solution: string): string[] => {
  const SHUFFLED_WORDS = shuffleList(WORDS);
  let phraseQueue = [solution];
  // for (let chinesePhrase of SHUFFLED_WORDS) {
  //   for (let chineseChar of solution) {
  //     if (chinesePhrase.includes(chineseChar)) {
  //       phraseQueue.push(chinesePhrase)
  //     }
  //     if (phraseQueue.length >= 6) {
  //       break;
  //     }  
  //   }
  //   if (phraseQueue.length >= 6) {
  //     break;
  //   }
  // };
  let uniqueSoln = new Set<string>()
  let finalOutput = new Set<string>();
  for (let c of solution) {
    uniqueSoln.add(c)
    finalOutput.add(c)
  }

  let cycles = 0;
  while (phraseQueue.length > 0 && cycles < 500) {
    let out = phraseQueue.shift()
    if (out == null){
      break;
    }
    let adjPhrases = getAdjPhrases(out, SHUFFLED_WORDS)
    for (let phrase of adjPhrases) {
      phraseQueue.push(phrase) 
      for (let c of phrase) {
        finalOutput.add(c)
        if (finalOutput.size >= 26) {
          break;
        }    
      }
      if (finalOutput.size >= 26) {
        break;
      }  
    }
    if (finalOutput.size >= 26) {
      break;
    }
    cycles++;
  }

  const result = shuffleList([...Array.from(finalOutput)]);
  return result;
}

export const { solution, solutionIndex } = getWordOfDay()
